import "./styles.scss";

import * as dat from "dat.gui";

import { Animation } from "./animation";

const animation = new Animation();

var gui = new dat.GUI({ name: "Controls" });

var controls = {
  play: () => {
    animation.play();
  },
  stop: () => {
    animation.stop();
  },
  pause: () => {
    animation.pause();
  },
  range: 0
};

gui.add(controls, "play");
gui.add(controls, "stop");
gui.add(controls, "pause");
gui
  .add(controls, "range", 1, animation.getDuration())
  .onChange(function (value) {
    animation.seek(Math.floor(value));
  });
//gui.show();
//gui.hide();
//animation.seek(0);
window.addEventListener("message", function (event) {
  const { frame, action, controls } = event.data;

  if (frame) {
    animation.seek(frame);
  }

  if (action === "play") {
    animation.play();
  }

  if (action === "pause") {
    animation.pause();
  }

  if (action === "stop") {
    animation.stop();
  }

  if (controls?.userControls) {
    animation.updateAnimation(controls.userControls);
  }
});
