import anime from "animejs";
import Zdog from "zdog";
import Zfont from "zfont";
import { rgbToHsl, hexToRgb } from "./utils";
import "./stylesAnimation.scss";

Zfont.init(Zdog);

export class Animation {
  displayName = "Animation chart horizontal bars";
  templateId = "chart_horizontal_columns";
  constructor() {
    console.log("loaded", this.displayName, "v", "1.0");
    this.font = new Zdog.Font({
      src:
        "https://cdn.jsdelivr.net/npm/font-roboto-local@1.0.1/fonts/roboto/Roboto-Light.ttf"
    });
    this.screen = document.getElementById("screen");
    this.animation = anime.timeline({ loop: true });
    this.aspect = 1.5;
    this.gap = 10;
    this.prefix = "$";
    this.postfix = "%";
    this.columnColor = "#003300";
    this.backgroundColor = "#274a60";
    this.textColor = "#ffffaa";
    this.direction = "column";
    this.cap = "round";
    this.intro = true;
    this.outro = true;
    this.data = [
      {
        title: "January",
        value: 15
      },
      {
        title: "February",
        value: 20
      },
      {
        title: "March",
        value: 10
      },
      {
        title: "May",
        value: 10
      },
      {
        title: "June",
        value: 15
      },
      {
        title: "July",
        value: 15
      },
      {
        title: "August",
        value: 50
      }
    ];

    this.createAnimation();
  }

  prepareData(inputData) {
    const data = [...inputData];
    return data;
  }

  createAnimation() {
    const width = 100 * this.aspect;
    const height = 100;
    this.screen.style = `
      flex-direction: ${this.direction}; 
      background: radial-gradient(circle at 50% 50%, ${this.backgroundColor} 0%, ${this.backgroundColor}F0 100%);
    `;
    const titleContainer = document.createElement("div");
    titleContainer.className = "title-container";
    this.screen.append(titleContainer);

    const title = document.createElement("div");
    title.innerHTML = this.title;
    title.className = "title";
    title.style = `color: ${this.textColor}`;
    titleContainer.append(title);

    const subtitle = document.createElement("div");
    subtitle.innerHTML = this.subtitle;
    subtitle.className = "subtitle";
    subtitle.style = `color: ${this.textColor}`;
    titleContainer.append(subtitle);

    const svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
    svg.setAttribute("viewBox", `0 0 ${width} ${height}`);
    svg.setAttribute("width", "100");
    svg.setAttribute("height", "100");
    svg.setAttribute("class", "illo");
    svg.setAttribute("style", "padding: 0");

    this.screen.appendChild(svg);

    let illo = new Zdog.Illustration({
      element: svg,
      dragRotate: true,
      onDragStart: function () {}
    });

    const yAngle = -Zdog.TAU / 10;
    const xAngle = -Zdog.TAU / 10;
    const scale = 100 / ((10 + this.gap) * this.data.length);

    let scene = new Zdog.Anchor({
      addTo: illo,
      rotate: {
        y: yAngle,
        z: 0,
        x: xAngle
      },
      scale: scale,
      translate: {
        x:
          (-(10 + this.gap) / 2) *
          (this.data.length - 1) *
          Math.cos(yAngle) *
          scale,
        y: 25
      }
    });

    const preparedDate = this.prepareData(this.data);

    preparedDate.forEach((item, index) => {
      let box = new Zdog.Box({
        addTo: scene,
        width: 10,
        height: 1,
        depth: 10,
        stroke: false,
        color: "#C25", // default face color
        leftFace: "#EA0",
        rightFace: "#E62",
        topFace: "#ED0",
        bottomFace: "#636",
        translate: {
          x: index * (10 + this.gap)
        }
      });

      var textLabel = new Zdog.Text({
        addTo: scene,
        font: this.font,
        value: item.title,
        fontSize: 9,
        stroke: false,
        textAlign: "left",
        textBaseline: "middle",
        color: "#fff",
        fill: true,
        translate: {
          z: 10,
          x: index * (10 + this.gap),
          y: 0
        },
        rotate: {
          x: Zdog.TAU / 4,
          z: Zdog.TAU / 4
          //y: Zdog.TAU / 4
        }
      });

      var textValue = new Zdog.Text({
        addTo: scene,
        font: this.font,
        value: "0",
        fontSize: 6,
        stroke: false,
        textAlign: "center",
        textBaseline: "middle",
        color: "#fff",
        fill: true,
        translate: {
          z: -10,
          x: index * (10 + this.gap) + 2.5,
          y: -1
        },
        rotate: {
          //x: Zdog.TAU / 4,
          //z: Zdog.TAU / 4,
          //y: Zdog.TAU / 4
        }
      });

      this.animation.add({
        targets: box,
        duration: 500,
        easing: "easeOutQuad",
        height: [1, item.value + 1],
        update: function (anim) {
          box.translate.y = -box.height / 2;
          textValue.translate.y = -box.height - 1;
          textValue.value = String(Math.round(box.height - 1));
          box.updatePath();
          scene.updateGraph();
          scene.renderGraphSvg(svg);
        }
      });
    });

    scene.translate.z = -100;

    scene.updateGraph();
    scene.renderGraphSvg(svg);

    parent.postMessage(
      {
        "animation.duration": this.animation.duration,
        template: this.templateId
      },
      "*"
    );
  }

  clearAnimation() {
    this.screen.replaceChildren();
  }

  updateAnimation(userControls) {
    if (userControls.title) {
      this.title = userControls.title;
    }
    if (userControls.subtitle) {
      this.subtitle = userControls.subtitle;
    }
    if (userControls.data) {
      this.data = userControls.data;
    }
    if (userControls.gap) {
      this.gap = userControls.gap;
    }
    if (userControls.columnColor) {
      this.columnColor = userControls.columnColor;
    }
    if (userControls.backgroundColor) {
      this.backgroundColor = userControls.backgroundColor;
    }
    if (userControls.textColor) {
      this.textColor = userControls.textColor;
    }
    if (userControls.direction) {
      this.direction = userControls.direction;
    }
    if (userControls.cap) {
      this.cap = userControls.cap;
    }
    this.animation = anime.timeline({ loop: true });
    this.clearAnimation();
    this.createAnimation();
  }

  play() {
    this.animation.play();
  }

  seek(frame) {
    const remFrame = frame % this.animation.duration;
    this.animation.pause();
    this.animation.seek(remFrame);
  }

  pause() {
    this.animation.pause();
  }

  stop() {
    this.animation.pause();
    this.animation.seek(1);
  }

  getDuration() {
    return this.animation.duration;
  }
}
